
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

export default {
name: 'FileLogin',
components: {
XHeader,
Group,
Calendar,
PopupPicker,
Datetime
},
data() {
return {
data: {},
profession: [],
list: [

[]
],
code: {},
yxq: this.$root.loginInfo.DZJYXQ,
}
},
mounted() {
this.getjob();
this.getdata()
},
methods: {
getjob() { //职业获取
this.$ajax.post(this.$root.host, {
'MSH.1': 'userCommon',
'MSH.2': 'zyZd',
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(arguments)

console.log(data)
if (typeof data != 'undefined') { //判断是否空对象
let self = this;
for (let i = 0; i < data.length; i++) {
const element = data[i].ZDVALUE;
self.list[0].push(data[i].ZDVALUE)
self.code[data[i].ZDVALUE] = data[i].ZDKEY
}
console.log(this.code)
}
})
},
getdata() { //获取用户信息
this.data = this.$root.loginInfo
this.profession.push(this.data.CHZZYMC) 
},
savedata() { //保存更改
this.$ajax.post('', {
'MSH.1': 'user',
'MSH.2': 'saveUserCompleteInfo',
'CHZZYBM': this.code[this.profession[0]], //职业编码
'CHZZY': this.profession[0], //职业
'DZJYXQ': this.yxq,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$root.loginInfo.CHZZY = this.code[this.profession[0]];
this.$root.loginInfo.CHZZYMC = this.profession[0];
this.$root.loginInfo.DZJYXQ = this.yxq;
this.$vux.toast.show({
text: "提交成功",
type: "success",
});
})
},
change(type) {
if(type){
this.savedata();
}
},
}
}



